import React from 'react'
import './Landing.css'
import './Card.css'
import './../Main.css'
import { useNavigate } from "react-router-dom";
import Footer from '../footer/Footer'
import Topbar from '../user/Topbar';
import { useAuth } from '../../firebase/AuthContext';

export default function Landing() {
    const navigate = useNavigate()
    const { currentUser } = useAuth()

    function handleMainRedirect() {
        navigate('/signup')
    }

    return (
        <div className="container">
            <Topbar />
            <div className="frame">
                <div className="frame-content">
                    <h1 className="header">Easy way to pay - Minebank</h1>

                    <Card />

                    <button onClick={handleMainRedirect} className="button center">{currentUser ? 'Your profile' : 'Get card'}</button>
                </div>
            </div>

            <div className="frame card">
                <h1 className="header">Why Minebank?</h1>
                <div className="info">
                    <div className="container-info">
                        <h1 className="ci-header">1. No payment fee </h1>
                        <p>We don`t charge any money for payments.</p>
                    </div>
                    <div className="container-info">
                        <h1 className="ci-header">2. Easy to use</h1>
                        <p>App is easy to use, just create an account and you are ready to do some transactions.</p>
                    </div>
                    <div className="container-info">
                        <h1 className="ci-header">3. Great customer service</h1>
                        <p>We value our customers providing timely, attentive, upbeat service to a customer.</p>
                    </div>
                </div>
            </div>

            {/* <div className="frame card">
                <h1 className='header'>Where?</h1>
                photo here
            </div> */}

            <Footer />
        </div>
    )
}

function Card() {
    return (
        <div className="card-panel">
            <p className="card-num">1234 5678 9876 5678</p>
            <p className="card-info">minepay</p>
        </div>
    )
}
