import React, { Fragment, useEffect, useState } from 'react'
import Footer from '../footer/Footer'
import Topbar from './Topbar'
import './Mineshop.css'
import './../Main.css'
import item from './item.png'
import { Navigate, useNavigate } from 'react-router-dom'
import { useAuth } from '../../firebase/AuthContext'
import UserNav from './UserNav'
import { Partition } from './User'
import { useSnackbar } from 'notistack'

export default function Mineshop() {
    const { getAllProducts, currentUser, getUserData } = useAuth()

    const [items, setItems] = useState([])
    const [userData, setUserData] = useState([])

    useEffect(() => {
        if (currentUser) {
            getUserData(currentUser.uid)
                .then(response => {
                    setUserData(response)
                })
            getAllProducts()
                .then(response => {
                    setItems(response)
                })
        }
    }, []);

    return (
        <Fragment>
            {!currentUser ? <Navigate to='/login' state={{ from: '/mineshop' }} /> : ''}

            <Topbar />

            <div className='main-container'>
                <Partition img={userData['img']} text={`${userData['nickname'] ? 'Hello, ' + userData['nickname'] + '!' : 'Loading data'}`} />


                <Promo name={'Winter sale!'} description={'Updated stock!'} />
                <UserNav />

                <Partition text={"All products"} />

                <div className='product-container'>
                    <GenerateProducts data={items} />
                </div>

                <Partition text={"Your orders"} />

                <GenerateOrders data={userData['recieved']} />

                <Partition text={"Your products"} />
                <div className='product-container'>
                    <GenerateUserProducts userData={userData} data={items} />
                    <NewProduct />
                </div>

                <Partition text={"Requested products"} />
                <GenerateRequested data={userData['requested']} user={userData} />
            </div>

            <Footer />
        </Fragment>
    )
}

function GenerateOrders(props) {
    let result = []

    if (props.data !== undefined)
        if (props?.data['length']) {
            props.data.forEach(product => {
                let statuses = ['Canceled', 'Waiting for seller', 'Done']

                result.unshift(<WideProduct name={`${product['name']} from ${product['from']}`} price={`${product['price']} Mc`} desc={statuses[parseInt(product['status'] + 1)]} img={product['img']} />)
            })
        } else {
            result.push(<NoData text="You haven`t made any orders yet" />)
        }

    return (
        <Fragment>
            {result}
        </Fragment>
    )
}

function GenerateProducts(props) {
    let doc = props.data
    let result = []

    doc.forEach((doc) => {
        let temp = doc.data()
        result.push(<ProductCard id={doc.id} by={temp['by']} price={temp['price']} name={temp['name']} url={temp['img']} />)
    })


    return (
        <Fragment>
            {result}
        </Fragment>
    )
}

function GenerateUserProducts(props) {
    let result = []

    let userItemId = props.userData['goods']

    props.data.forEach((doc) => {
        let temp = doc.data()

        if (userItemId.includes(doc.id))
            result.push(<UserProductCard id={doc.id} by={'you'} price={temp['price']} name={temp['name']} url={temp['img']} description={temp['description']} />)
    })

    return (
        <Fragment>
            {result}
        </Fragment>
    )
}

function WideProduct(props) {
    return (
        <div className='transaction-card'>
            <img className='ts-img no-bg' src={props.img} onError={(e) => { e.target.onerror = null; e.target.src = item }} />
            <p className='ts-name'>{props.name}</p>
            <p className='ts-desc'>{props.desc}</p>
            <p className='ts-amount'>{props.price}</p>
        </div>
    )
}

function GenerateRequested(props) {
    let result = []

    if (props.data !== undefined)
        if (props.data['length']) {
            props.data.forEach((product, id) => {
                result.unshift(<RequestedProduct id={id} user={props.user} productData={product} done={product['done']} status={parseInt(product['status'])} name={`${product['product']['name']} for ${product['name']}`} price='buttons' desc={`${product['product']['price']} Mc${product['product']['address'] ? ',' : ''} ${product['product']['address']}`} img={product['product']['img']} />)
            })
        } else {
            result.push(<NoData text="You don`t have any requested products" />)
        }

    return (
        <Fragment>
            {result}
        </Fragment>
    )
}

function RequestedProduct(props) {
    const { enqueueSnackbar } = useSnackbar()
    const { updateOrderStatus, currentUser } = useAuth()

    const [status, setStatus] = useState(props.status)

    let buttons = null

    if (props.done) {
        let statuses = ['Canceled', 'Processing', 'Done']

        buttons = <button className='product-status-button-disabled'>{statuses[props.status + 1]}</button>
    } else {
        buttons = <div className='ts-button-container '>
            <button className={`product-status-button ${status === 1 ? 'product-button-active' : ''}`} onClick={(e) => { setStatus(1) }}>Done</button>
            <button className={`product-status-button ${status === 0 ? 'product-button-active' : ''}`} onClick={(e) => { setStatus(0) }}>Processing</button>
            <button className={`product-status-button ${status === -1 ? 'product-button-active' : ''}`} onClick={(e) => { setStatus(-1) }}>Cancel</button>
            <button className={'product-status-button product-button-save'} onClick={(e) => {
                e.preventDefault()

                updateOrderStatus(props.productData, props.user, status, true, props.id, currentUser.uid)
                    .catch(err => {
                        enqueueSnackbar('Error', { variant: 'error' })
                    })
                    .then(() => {
                        enqueueSnackbar('Status updated', { variant: 'success' })
                    })
            }}>Save</button>
        </div>
    }

    return (
        <div className='transaction-card wide'>
            <img className='ts-img no-bg' src={props.img} onError={(e) => { e.target.onerror = null; e.target.src = item }} />
            <p className='ts-name'>{props.name}</p>
            <p className='ts-desc'>{props.desc}</p>
            <div className='ts-amount'>
                {buttons}
            </div>
        </div>
    )
}

export function NoData(props) {
    return (
        <div className='no-data'>
            {props.text}
        </div>
    )
}

function NewProduct() {
    const { enqueueSnackbar } = useSnackbar()

    const [img, setImg] = useState(null)
    const [name, setName] = useState(null)
    const [price, setPrice] = useState(null)
    const [description, setDescription] = useState(null)

    const { createProduct, currentUser, getUserData } = useAuth()

    return (
        <div className='product-card padding-bottom'>
            <div>Img url</div>
            <input className='input whitesmoked' onChange={(e) => { setImg(e.target.value) }} />
            <div>Product name</div>
            <input className='input whitesmoked' onChange={(e) => { setName(e.target.value) }} />
            <div>Price</div>
            <input className='input whitesmoked' onChange={(e) => { setPrice(e.target.value) }} />
            <div>Description</div>
            <input className='input whitesmoked' onChange={(e) => { setDescription(e.target.value) }} />

            <div className='edit-button-container'>
                <button className='edit-button' onClick={() => {
                    if (currentUser) {
                        if (img && name && description && Number.isInteger(parseFloat(price)) && price > 0 && price < 1000000) {
                            getUserData(currentUser.uid)
                                .then((res) =>
                                    createProduct(img, name, price, description, res['nickname'], currentUser.uid, res['goods'])
                                        .then(() => {
                                            enqueueSnackbar('Product created', { variant: 'success' })
                                        })
                                )
                        } else {
                            console.log(price)
                            enqueueSnackbar('Enter valid data', { variant: 'error' })
                        }
                    }
                }}>Create</button>
            </div>
        </div >
    )
}

function UserProductCard(props) {
    const { enqueueSnackbar } = useSnackbar()

    const [isEdit, setIsEdit] = useState(false)
    const { updateProduct } = useAuth()

    const [url, setUrl] = useState(props.url)
    const [name, setName] = useState(props.name)
    const [price, setPrice] = useState(props.price)
    const [description, setDescription] = useState(props.description)

    let card = ''

    if (isEdit) {
        card = <div className='product-card'>
            <div>Img url</div>
            <input className='input whitesmoked' value={url} onChange={(e) => setUrl(e.target.value)} />
            <div>Product name</div>
            <input className='input whitesmoked' value={name} onChange={(e) => setName(e.target.value)} />
            <div>Price</div>
            <input className='input whitesmoked' value={price} onChange={(e) => setPrice(e.target.value)} />
            <div>Description</div>
            <input className='input whitesmoked' value={description} onChange={(e) => setDescription(e.target.value)} />

            <div className='edit-button-container'>
                <button className='edit-button' onClick={(e) => {
                    e.preventDefault()

                    if (url && name && description && Number.isInteger(parseFloat(price)) && price > 0 && price < 1000000) {

                        updateProduct(props.id, url, name, price, description)
                            .then(() => {
                                enqueueSnackbar('Product updated', { variant: 'success' })
                            })
                    } else {
                        enqueueSnackbar('Enter valid data', { variant: 'error' })
                    }
                }
                }>Save</button>
                <button className='edit-button' onClick={(e) => {
                    e.preventDefault()

                    setIsEdit(false)
                }}>Cancel</button>
            </div>
        </div>
    } else {
        card = <div className='product-card'>
            <img className='product-img' src={props.url} onError={(e) => { e.target.onerror = null; e.target.src = item }} />
            <div className='product-name'>{props.name}</div>
            <div className='product-price'>{props.price} <span className='thin'>Mc</span></div>
            <div className='product-detail'>By {props.by}</div>

            <button className='product-button' onClick={(e) => {
                e.preventDefault()

                setIsEdit(true)
            }}>Edit</button>
        </div>
    }

    return (
        <div>
            {card}
        </div>
    )
}

function ProductCard(props) {
    let navigate = useNavigate();

    return (
        <div className='product-card'>
            <img className='product-img' src={props.url} onError={(e) => { e.target.onerror = null; e.target.src = item }} />
            <div className='product-name'>{props.name}</div>
            <div className='product-price'>{props.price} <span className='thin'>Mc</span></div>
            <div className='product-detail'>By {props.by}</div>
            <button className='product-button' onClick={() => { navigate(`${props.id}`) }}>View</button>
        </div>
    )
}

function Promo(props) {
    return (
        <div className='promo-card'>
            <div className='promo-name'>{props.name}</div>
            <div className='promo-description'>{props.description}</div>
        </div>
    )
}