import { Routes, Route } from "react-router";
import { useNavigate } from 'react-router-dom';
import Landing from "./pages/landing/Landing";
import Login from "./pages/user/Login";
import Signup from "./pages/user/Signup";
import User from "./pages/user/User";
import './pages/Main.css'
import AuthContextProvider from "./firebase/AuthContext";
import Mineshop from "./pages/user/Mineshop";
import ViewProduct from "./pages/user/ViewProduct";
import Profile from "./pages/user/Profile";
import Calculators from "./pages/user/Calculators";


function App() {

  return (
    <AuthContextProvider>
      <Routes>
        <Route path="user" element={<User />} />
        <Route path="mineshop" element={<Mineshop />} />
        <Route path="mineshop/:productId" element={<ViewProduct />} />
        <Route path="profile" element={<Profile />} />
        <Route path="calculators" element={<Calculators />} />
        <Route path="login" element={<Login />} />
        <Route path="signup" element={<Signup />} />
        <Route path="*" element={<Landing />} />
      </Routes>
    </AuthContextProvider >
  );
}

export default App;
