import React, { useEffect, useState } from 'react'
import { Navigate } from 'react-router-dom'
import { useAuth } from '../../firebase/AuthContext'
import Footer from '../footer/Footer'
import Error from './Error'
import Topbar from './Topbar'
import item from './item.png'
import UserNav from './UserNav'
import { useSnackbar } from 'notistack';
import graySquare from './profile.png'

export default function User() {
    const { enqueueSnackbar } = useSnackbar()

    const { currentUser, getUserData } = useAuth()
    const [userData, setUserData] = useState({ diamonds: '-' })

    useEffect(() => {
        if (currentUser) {
            getUserData(currentUser.uid)
                .then(response => {
                    setUserData(response)
                })
        }
    }, []);

    return (
        <div>
            {!currentUser ? <Navigate to='/login' state={{ from: '/user' }} /> : ''}
            <Topbar />
            <div className='main-container'>
                <Partition img={userData['img']} text={`${userData['nickname'] ? 'Hello, ' + userData['nickname'] + '!' : 'Loading data'}`} />
                <Card amount={userData['diamonds']} />

                <UserNav />

                <Partition text={'Transfer'} />
                <Transfer data={userData} />

                <GenerateTransactions data={userData["transactions"]} />
            </div>
            <Footer />
        </div>
    )
}

export function Partition(props) {
    let img = ''
    if (props.img) {
        img = <img src={props.img} className='profile-pic' onError={(e) => { e.target.onerror = null; e.target.src = graySquare }} />
    }
    return (
        <div className='partition-container'>
            {img}
            <span className='partition-name'>{props.text}</span>
        </div>
    )
}

export function Card(props) {
    return (
        <div className='wide-card'>
            <p className='wc-amount'><span className='big-font'>{props.amount}</span> minecoins</p>
            <p className='wc-system'>minepay</p>
        </div>
    )
}

function Transfer(props) {
    const { enqueueSnackbar } = useSnackbar()
    const { transfer } = useAuth()

    const user = props.data
    const [reciverNickname, setReciverNickname] = useState(null)
    const [reciverAmount, setReciverAmount] = useState(null)
    const [reciverText, setReciverText] = useState(`Money transfer`)

    const [loading, setLoading] = useState(false)
    const [status, setStatus] = useState(null)
    const [description, setDescription] = useState('description')

    function handleSend() {
        setLoading(true)

        if (!(reciverNickname && reciverAmount) || (reciverAmount < 1 || reciverAmount > 999999) || (parseInt(reciverAmount) !== parseFloat(reciverAmount))) {
            setStatus('error')
            setDescription('Enter valid data')
            setLoading(false)
            snackbar('Enter valid data', { variant: 'error' })
        } else if (reciverNickname === user["nickname"]) {
            setStatus('error')
            setDescription('Error: you can`t transfer money to yourself')
            setLoading(false)
            snackbar('Error: you can`t transfer money to yourself', { variant: 'error' })
        } else {
            transfer(reciverNickname, reciverAmount, reciverText, user['nickname'])
                .then((res) => {
                    setLoading(false)
                    if (res !== 'success') {
                        setStatus('error')
                        setDescription(res)
                        snackbar(res, { variant: 'error' })
                    } else {
                        setStatus('success')
                        setDescription('Payment successfull')
                        snackbar('Payment successfull', { variant: 'success' })
                    }
                })
        }
    }

    function snackbar(description, data) {
        enqueueSnackbar(description, data)
    }

    return (
        <div className='transfer-container'>
            <p className='tc-name'>To:</p>
            <input className='input' type='text' placeholder='recipient nickname' onChange={(e) => { setReciverNickname(e.target.value) }} />
            <p className='tc-name'>Amount:</p>
            <input className='input' type='number' placeholder='' onChange={(e) => { setReciverAmount(e.target.value) }} step="1" />
            <p className='tc-name'>Description:</p>
            <input className='input' type='text' onChange={(e) => { setReciverText(e.target.value) }} maxLength={100} placeholder={`Money transfer`} />

            {loading === true ?
                <button className='button disabled' disabled={true}>Pending...</button>
                :
                <button className='button' onClick={handleSend}>Send</button>}
        </div>
    )
}

function GenerateTransactions(props) {
    let transactionList = props.data
    let result = []

    if (transactionList !== undefined) {
        result.push(<Partition text={'All transactions'} />)
        for (let i = Object.keys(transactionList).length - 1; i >= 0; i -= 1) {
            result.push(<Transaction img={transactionList[i]['img']} name={transactionList[i]['who']} desc={transactionList[i]['description']} amount={transactionList[i]['amount']} />)
        }
    }
    return (
        <div>
            {result}
        </div>
    )
}

function Transaction(props) {
    let profilePic = ''

    if (props.img?.length === 0) {
        profilePic = <div className='ts-img initial-letter'>{props.name[0].toUpperCase()}</div>
    } else {
        profilePic = <img className='ts-img' src={props.img} />
    }
    return (
        <div className='transaction-card'>
            {profilePic}
            <p className='ts-name'>{props.name}</p>
            <p className='ts-desc'>{props.desc}</p>
            <p className={'ts-amount ' + (props.amount[0] === '+' ? 'green' : '')}>{props.amount}</p>
        </div>
    )
}