import React, { useEffect, useState } from 'react'
import Footer from '../footer/Footer'
import Topbar from './Topbar'
import { Card, Partition } from './User'
import { useAuth } from '../../firebase/AuthContext'
import UserNav from './UserNav'
import { NoData } from './Mineshop'

export default function Calculators() {
    const { currentUser, getUserData } = useAuth()
    const [userData, setUserData] = useState({ diamonds: '-' })

    useEffect(() => {
        if (currentUser) {
            getUserData(currentUser.uid)
                .then(response => {
                    setUserData(response)
                })
        }
    }, []);

    return (
        <div>
            <Topbar />
            <div className='main-container'>
                <Partition img={userData['img']} text={`${userData['nickname'] ? 'Hello, ' + userData['nickname'] + '!' : 'Loading data'}`} />
                <Card amount={userData['diamonds']} />
                <UserNav />
                <NoData text='Coming soon' />
                <NoData text='btw why are you here?' />
                {/* <Partition text='Item' /> */}

                {/* <Partition text='Distance' /> */}
            </div>
            <Footer />
        </div>
    )
}
