import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { useAuth } from '../../firebase/AuthContext'
import './Topbar.css'
import './../Main.css'

export default function Topbar(props) {
    const navigate = useNavigate()

    const { logout, currentUser } = useAuth()
    // console.log(currentUser + 'asadsadsadasda')
    return (
        <div className="topbar">
            <Link to="/home" className="link left">Minebank<sup>beta</sup></Link>

            {(currentUser ?
                <p className="link right login" onClick={async e => {
                    e.preventDefault()
                    logout()
                    // navigate('/home')
                }}>Log out</p>
                :
                <Link className="link right login" to="/login">Log in</Link>
            )}
        </div>
    )
}
