import React from 'react'
import { Link, Navigate, useLocation, useNavigate } from 'react-router-dom';
import Topbar from './Topbar'
import { useState } from 'react'
import { useAuth } from '../../firebase/AuthContext';
import { useSnackbar } from 'notistack';

export default function Login() {
    const { enqueueSnackbar } = useSnackbar()

    const [loading, setLoading] = useState(false)
    const [errorDesc, setErrorDesc] = useState('first')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const location = useLocation()
    const { login, currentUser } = useAuth()
    const navigate = useNavigate()

    console.log(location)
    if (currentUser) {
        navigate(location.state?.from ?? '/user')
    }

    return (
        <div>
            <Topbar />
            <div className='main-container'>
                <div className='transfer-container'>
                    <p className='partition-name center'>Welcome back!</p>
                    <p className='tc-name'>Username:</p>
                    <input value={email} onChange={(e) => setEmail(e.target.value)} className='input' type='text' placeholder='example@gmail.com' />
                    <p className='tc-name'>Password:</p>
                    <input value={password} onChange={(e) => setPassword(e.target.value)} className='input' type='password' placeholder='' />

                    {
                        loading ?
                            <button disabled={1} className='button disabled'>Log In</button>
                            :
                            <button disabled={loading} onClick={async e => {
                                e.preventDefault()
                                setLoading(true)

                                if (email && password) {
                                    login(email, password)
                                        // .then(response => console.log(response))
                                        .catch(e => {
                                            setErrorDesc(e.message)
                                            enqueueSnackbar(e.message, { variant: 'error' })
                                        })
                                        .finally(() => {
                                            setLoading(false)
                                        })
                                } else {
                                    setErrorDesc('Enter valid data')
                                    setLoading(false)
                                    enqueueSnackbar('Enter valid data', { variant: 'error' })
                                }
                            }} className='button' > Log in</button>
                    }

                    <p className='subtext'>Don`t have an acoount? <Link className='link' to='/signup'>Sign up</Link></p>
                </div>
            </div>
        </div >
    )
}
