import React, { Fragment, useEffect, useState } from 'react'
import { Navigate, useNavigate, useParams } from "react-router-dom";
import Footer from '../footer/Footer';
import Topbar from './Topbar';
import './../Main.css'
import { useAuth } from '../../firebase/AuthContext';
import './ViewProduct.css'
import { useSnackbar } from 'notistack';
import item from './item.png'
import back from './back.svg'

export default function ViewProduct() {
    const { enqueueSnackbar } = useSnackbar()

    let params = useParams();
    let res = []

    const { getProduct, currentUser, getUserData, buyProduct } = useAuth()
    const [data, setData] = useState([])
    const [userData, setUserData] = useState([])

    const navigate = useNavigate()

    useEffect(() => {
        window.scrollTo(0, 0)
        getProduct(params.productId)
            .then((res) => {
                setData(res.data())
            })
        if (currentUser) {
            getUserData(currentUser.uid)
                .then(response => {
                    setUserData(response)
                })
        }
    }, []);

    if (data) {
        res = <Fragment>
            <Topbar />
            <div className='main-container'>
                <div className='vp-back-container' onClick={() => { navigate('/mineshop') }}>
                    <img className='vp-back-arrow' src={back} />
                    <div className='vp-back-text'>Go back</div>
                </div>

                <div className='vp-product-container'>
                    <img className='vp-img' src={data['img']} onError={(e) => { e.target.onerror = null; e.target.src = item }} />
                    <div className='vp-info-container'>
                        <h1 className='vp-name'>{data['name']}</h1>
                        <div className='vp-price'>{data['price']} <span className='thin'>Mc</span></div>
                        <p className='vp-description'>{data['description']}</p>
                        <button className='vp-button' onClick={(e) => {
                            e.preventDefault()

                            buyProduct(data, data['uid'], userData, currentUser.uid)
                                .then(res => {
                                    if (res === 'ok') {
                                        enqueueSnackbar('Order created', { preventDuplicate: false, variant: 'success' })
                                    } else {
                                        enqueueSnackbar(res, { variant: 'error' })
                                    }
                                })
                            // .then((res) => console.log(res))
                        }}>Purchase</button>
                    </div>
                </div>

                <Partition text={'Reviews'} />

                <NewReview id={params.productId} />
                <GenerateReviews data={data['reviews']} />

            </div>

            <Footer />
        </Fragment>
    } else {
        res = <Fragment>
            <Topbar />
            <div className='main-container'>
                <div className='vp-back-container' onClick={() => { navigate(-1) }}>
                    <img className='vp-back-arrow' src={back} />
                    <div className='vp-back-text'>Go back</div>
                </div>

                <NoData text='Looks like this product doesn`t exist' />
            </div>
            <Footer />
        </Fragment>
    }

    return (
        <Fragment>
            {!currentUser ? <Navigate to='/login' state={{ from: '/mineshop/' + params.productId }} /> : ''}
            {res}
        </Fragment>
    )
}

function NewReview(props) {
    const { enqueueSnackbar } = useSnackbar()

    const { currentUser, getUserData, addReview } = useAuth()

    const [rating, setRating] = useState(50)
    const [description, setDescription] = useState('')

    return (
        <div className='review-container'>
            <div className='bold'>Your review: </div>
            <input className={`range pointer ${(rating < 20 ? '' : rating < 30 ? 'orange' : (rating < 40 ? 'green' : 'lime'))}`} type="range" min="0" max="50" value={rating} onChange={(e) => { setRating(e.target.value) }} />
            <span className='review-rating'>
                {rating / 10}/5
            </span>

            <input type='text' className='input whitesmoked left-float' placeholder='Type what you think about product' onChange={(e) => { setDescription(e.target.value) }} />
            <button type='submit' className='publish-button' onClick={(e) => {
                e.preventDefault()
                let userInfo = getUserData(currentUser.uid)
                    .then((res) => {
                        // console.log(`${res['nickname']} ${rating} ${description} ${props.id}`)

                        addReview(props.id, res['nickname'], rating, description)
                            .catch((err) => {
                                enqueueSnackbar(err, { variant: 'error' })
                            })
                            .then(() => {
                                enqueueSnackbar('Review posted', { variant: 'success' })
                            })
                    })
            }}>Publish</button>
        </div>
    )
}

function GenerateReviews(props) {
    let data = props.data
    let result = []
    let temp = []
    let sum = 0

    if (data !== undefined && Object.keys(data).length !== 0) {
        for (let i = Object.keys(data).length - 1; i >= 0; i -= 1) {
            temp.push(<Review name={data[i]['by']} rating={data[i]['rating']} description={data[i]['description']} />)
            sum += parseFloat(data[i]['rating'])
        }
        if (Object.keys(data).length > 1)
            result.push(<Review name='AVERAGE SCORE' rating={Math.round(sum / Object.keys(data).length * 10) / 10} />)
        result.push(temp)
    } else {
        result.push(<NoData text='No reviews yet' />)
    }

    return (
        <div>
            {result}
        </div>
    )
}

function Review(props) {
    let rating = props.rating
    let name = props.name
    let description = props.description

    return (
        <div className='review-container'>
            <span className='bold'>{name}</span>
            <input className={`range lr-margin ${(rating < 20 ? '' : rating < 30 ? 'orange' : (rating < 40 ? 'green' : 'lime'))} no-cursor`} type="range" min="0" max="50" value={rating} disabled />
            <span className='review-rating lr-margin'>
                {rating / 10}/5
            </span>
            <p>
                {description}
            </p>
        </div>
    )
}

function Partition(props) {
    let img = ''
    if (props.img) {
        img = <img src={props.img} className='profile-pic' />
    }
    return (
        <div className='partition-container'>
            {img}
            <span className='partition-name'>{props.text}</span>
        </div>
    )
}

function NoData(props) {
    return (
        <div className='no-data'>
            {props.text}
        </div>
    )
}